.confirmorder {
  margin-top: 5vmax;
  min-height: 100vh;
}
.confirmOrderPage {
  background-color: #fff;
  display: grid;
  grid-template-areas:
    "confirmCartItems  confirmCartItems confirmCartItems confirmCartItems confirmCartItems confirmCartItems confirmShippingArea "
    "orderSummary orderSummary orderSummary orderSummary orderSummary orderSummary orderSummary ";
  padding: 5vmax;
  padding-top: 2vmax;
}

.confirmShippingArea {
  grid-area: confirmShippingArea;
  border-left: 1px solid rgba(0, 0, 0, 0.349);
  padding-left: 3vmax;
}

/* .confirmShippingAreaBox,
.confirmCartItemsContainer {
  margin: 2vmax;
} */

.confirmShippingAreaBox > div {
  display: flex;
  margin: 1vmax 0;
}
.confirmShippingAreaBox > div > p {
  font: 400 1vmax "Roboto";
  color: black;
}
.confirmShippingAreaBox > div.address > span {
  width: 15vmax;
}
.confirmShippingAreaBox > div > span {
  font: 100 1vmax "Roboto";
  margin: 0 1vmax;
  color: #575757;
}
.confirmCartItems {
  grid-area: confirmCartItems;
}
.orderSummary {
  grid-area: orderSummary;
}
.confirmCartItems > p,
.orderSummary > p,
.confirmShippingArea > p {
  font: 400 1.8vmax "Roboto";
  text-decoration: underline;
}
/* .confirmCartItems {
  padding: 5vmax;
  padding-top: 2vmax;
  padding-bottom: 0;
} */
.confirmCartItemsContainer {
  max-height: 20vmax;
  overflow-y: auto;
}
.confirmCartItemsContainer > div {
  font: 400 1vmax "Roboto";
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2vmax 0;
}

.confirmCartItemsContainer > div > a {
  text-decoration: none;
  color: #575757;
  margin: 0 2vmax;
  font: 100 1.5vmax "Roboto";

  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.confirmCartItemsContainer > div > a > img {
  width: 5vmax;
  margin-right: 2vmax;
}

.confirmCartItemsContainer > div > span {
  margin-right: 1rem;
  font: 100 1.5vmax "Roboto";
  color: #5e5e5e;
}
/* .orderSummary {
  padding: 2vmax;
  padding-bottom: 0;
} */
.orderSummary > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.orderSummary > div > div {
  margin: 1vmax;
}

.orderSummary > div > div:first-child {
  margin-left: 0;
}
.orderSummary > div > div > p {
  display: inline;
  margin-right: 0.5vmax;
  font: 400 1.3vmax "Roboto";
}

.orderSummary > div > div > span {
  /* text-decoration: underline; */
  font: 400 1.3vmax "Roboto";
}

.proceedToPay {
  animation: zoomInOut 0.5s infinite;
}
.proceedToPay:hover {
  animation-play-state: paused;
}
@keyframes zoomInOut {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.orderSummary > div > div:last-child > button {
  padding: 1vmax 3vmax;
}
@media screen and (max-width: 600px) {
  .confirmOrderPage {
    padding: 3vmax;
    padding-top: 0;
    grid-template-areas:
      "confirmCartItems  "
      "confirmShippingArea  "
      "orderSummary  ";
  }
  .confirmCartItems > p,
  .orderSummary > p,
  .confirmShippingArea > p {
    font: 400 3vmax "Roboto";
  }
  .confirmShippingArea {
    border: none;
  }
  .orderSummary > div {
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: column;
  }
  .orderSummary > div > div {
    margin: 0.5vmax;
  }

  .confirmShippingAreaBox > div > p {
    font: 400 2.5vmax "Roboto";
    color: black;
  }
  .confirmShippingAreaBox > div.address > span {
    width: 100%;
  }
  .confirmShippingAreaBox > div > span {
    font: 400 2.5vmax "Roboto";
  }
  .confirmShippingArea {
    padding-left: 0;
  }

  .confirmCartItemsContainer > div > a {
    font: 100 3vmax "Roboto";
  }

  .confirmCartItems {
    margin: 0;
  }
  /* .orderSummary,
  .confirmShippingArea {
    margin-top: 5vmax;
  } */
  .confirmCartItemsContainer > div > a > img {
    width: 10vmax;
    margin-right: 2vmax;
  }

  .confirmCartItemsContainer > div > span {
    font: 100 2vmax "Roboto";
    color: #5e5e5e;
  }

  .orderSummary > div > div > p {
    font: 400 2.5vmax "Roboto";
  }

  .orderSummary > div > div > span {
    font: 400 2.5vmax "Roboto";
  }

  .proceedToPay {
    animation-name: unset;
  }

  .orderSummary > div > div:last-child > button {
    padding: 2vmax 5vmax;
    font: 400 2.5vmax "Roboto";
    margin: 0;
  }
}
